import React,{useEffect} from "react";
import Container from '../Elementos/Container'
import Titulo from "../Elementos/Titulo";
import Html5Fill from 'remixicon-react/Html5FillIcon';
import SubTitle from "../Elementos/SubTitles";
import CssFill from 'remixicon-react/Css3FillIcon';
import ReactFill from 'remixicon-react/ReactjsFillIcon';
import  Js from '../img/js.png'
import Css from '../img/css.png';
import CodeFill from'remixicon-react/CodeFillIcon'
import CodeSlash from 'remixicon-react/CodeSSlashFillIcon'
import ScrollReveal from "scrollreveal";

const Skills = () =>{
    useEffect(
        ()=>{
          const sr = ScrollReveal(
            {
              origin:'top',
              distance:'90px',
              duration:2000,
              reset:true
            }
          );
          sr.reveal(
              '.title-skills',
              {
                  opacity:0,
                  interval:300
              }
          )

          sr.reveal(
              '.content_tecnologia_info',
              {
                opacity:0,
                origin:'left',
                interval:300
              }
          )
          
        
        }
     ,[])

    return(
        <Container  column id='Skills'>
           <Titulo about className="title-skills">
               Habilidades
               <span className='about_span'>Tecnologias que domino</span>
            </Titulo>
            <CodeFill className='iconos skill-icon'></CodeFill>
            <div className='content_tecnologias_domino'>
                 <div className='content_tecnologia_info' >
                 <span className='skill-porcent'>80%</span>
                     <Html5Fill className='icon html'/>
                    <div className='content_data'>
                     <SubTitle>HTML5</SubTitle>
                     <p className='description html_info'>
                     El Lenguaje de Marcado de Hipertexto (HTML) es el código que se utiliza para estructurar y desplegar una página web y sus contenidos. Por ejemplo, sus contenidos podrían ser párrafos, una lista con viñetas, o imágenes y tablas de datos.
                     </p>
                    </div>   
                 </div>
                 <div className='content_tecnologia_info css' >
                 <span className='skill-porcent'>50%</span>
                     <img src={Css} className='icon css'/>
                    <div className='content_data'>                        
                        <SubTitle>CSS</SubTitle>
                        <p className='description html_info'>
                        CSS es un lenguaje de diseño gráfico que permite definir y crear la presentación de un documento estructurado escrito en un lenguaje de marcado. Es muy usado para establecer el diseño visual de los documentos web e interfaces de usuario escritas en HTML
                        </p>
                    </div>
                 </div>
                 <div className='content_tecnologia_info React' >
                 <span className='skill-porcent'>60%</span>
                     <ReactFill className='icon  react'/>
                    <div className='content_data'>
                        <SubTitle>React</SubTitle>
                        <p className='description html_info'>
                        React te ayuda a crear interfaces de usuario interactivas de forma sencilla. Diseña vistas simples para cada estado en tu aplicación, y React se encargará de actualizar y renderizar de manera eficiente los componentes correctos cuando los datos cambien.
                        </p>
                    </div>
                 </div>
                 <div className='content_tecnologia_info js' >
                 <span className='skill-porcent'>65%</span>
                     <img src={Js} className='icon js'/>
                     <div className='content_data'>
                     <SubTitle>JavaScript</SubTitle>
                     <p className='description html_info'>
                     JavaScript es el lenguaje de programación que debes usar para añadir características interactivas a tu sitio web, (por ejemplo, juegos, eventos que ocurren cuando los botones son presionados o los datos son introducidos en los formularios, efectos de estilo dinámicos, animación, y mucho más).
                     </p>
                    </div>  
                 </div>                                                          
            </div>
            <CodeSlash className='iconos'></CodeSlash>
        </Container>
    )
}


export default Skills;