import Titulo from "../Elementos/Titulo";
import Face from '../img/fb.svg';
import Git from '../img/git.svg';
import Ins from '../img/ins.svg';
import React,{useEffect} from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCode } from '@fortawesome/free-solid-svg-icons'
import Img1 from '../img/mask2.png';
import Img2 from '../img-home.jpeg';
import ScrollReveal from "scrollreveal";



const Home = () =>{

    useEffect(
        ()=>{
          const sr = ScrollReveal(
            {
              origin:'top',
              distance:'90px',
              duration:2000,
              reset:true
            }
          );
          sr.reveal(
              '.content_home_text',
              {
                  opacity:0,
                  interval:300
              }
          )

          sr.reveal(
              '.content_social',
              {
                opacity:0,
                origin:'left',
                interval:300
              }
          )
          
        
        }
     ,[])
  
    const AddTextAnimation = () =>{
       
      let target = document.querySelector('.text-animation')
        
      target.classList.add('AnimationText')
    }
    window.addEventListener('DOMContentLoaded',AddTextAnimation)


    return(
        <div className='content_home'>
            <div className='content_home_text'>
                <div className='figure-content iconFigura'>
                   <FontAwesomeIcon icon={faCode} className='icon-code'/> 
                </div>
                <Titulo 
                    grande className='text-animation'
                >
                    Desarrollador Web
                </Titulo>
                <p className='description home-text-description'>Hola , soy Abel y tengo 18 años ,
                    amante de la Programación y <br/>
                    con ganas de seguir aprendiendo.
                    Hago sitios webs :) .</p>
                <div className='btns-info'>
                    <button>Contactame</button>
                    <button>Conóceme más</button>
                </div>
            </div>
            <ul className='content_social'>
                 <li className='wrapper_icon_social'>
                     <img src={Face} className='item_social'/>
                 </li>
                 <li className='wrapper_icon_social'>
                     <img src={Git} className='item_social'/>
                 </li>   
                 <li className='wrapper_icon_social'>
                     <img src={Ins} className='item_social'/>
                 </li>                                
            </ul>
            <div className='blend'>
           <img src={Img2}></img>
           <img src={Img1} className='mask'></img>
            </div>
        </div>
    )
}


export default Home;