import Container from "../Elementos/Container"
import Titulo from "../Elementos/Titulo";
import AboutImg from "../img/about-img.jpeg";
import Btn from "../Elementos/Btn";
import ScrollReveal from "scrollreveal";
import React,{useEffect} from 'react'


const About = () =>{

    useEffect(()=>{
       const sr = ScrollReveal({
        origin:'top',
        distance:'90px',
        duration:2000,
        reset:true
       })
      
       sr.reveal(
           '.about-title'
       )
       sr.reveal(
           '.about_content_informacion_img',
           {
               origin:'left',
               opacity:0,
               interval:300
           }
       )
       sr.reveal(
        '.about_content_informacion_description',
        {
            origin:'right',
            opacity:0,
            interval:300
        }
    )

    },[])

    return(
       <Container pequeño id='About'>
           <Titulo about  className='about-title'>
               Sobre Mi 
               <span className='about_span'>Introduccion</span>
           </Titulo>
           <div className='about_content_informacion'>
                 <div className='about_content_informacion_img'>
                     <img  src={AboutImg} alt='computadoras'/>
                 </div>
                 <div className='about_content_informacion_description'>
                    <p className='description left' >
                      Soy una persona autodidacta que empezo en este rubro con cursos online y cada dia me  empezaba a atraer más , me gusta construir sitios web medornos pero simpre intentando mejorar cada 
                      dia , mi  meta es dejar mi huella en este mundo de la programacion  y formar parte de algo grande.
                    </p>
                    <Btn className="btn-demo">
                        Contactame
                    </Btn>
                 </div>   
           </div>
       </Container>
    )
}

export default About;