import Container from '../Elementos/Container'
import SubTitle from '../Elementos/SubTitles';
import Titulo from '../Elementos/Titulo';
import React,{useEffect} from 'react'
import Ilustracion from '../img/designer.svg'
import CodeFillIcon from 'remixicon-react/CodeFillIcon';
import Disign from '../img/diseñoWeb.svg'
import BookDesign from 'remixicon-react/BookMarkFillIcon'
import ScrollReveal from 'scrollreveal';

const Servicios = () =>{
    
    useEffect(
        ()=>{
         const sr = ScrollReveal({
            origin:'top',
            distance:'40px',
            duration:2000,
            reset:true
         })

         sr.reveal(
             '.title-services'
         )
         sr.reveal(
            '.primero',{
                opacity:0,
                origin:'left',
                interval:300
            }
        )
        sr.reveal(
            '.segundo',{
                opacity:0,
                origin:'right',
                interval:300
            }
        )
        }
    ,[])



    return(

        <Container  column id='Services'>
            <Titulo about className='title-services'>Servicios</Titulo>

            <div className='content_servicios '>
               <div className='content_servicio primero'>
                   <SubTitle style={{margin:'20px 0', position:'relative', zIndex:10, top:'20px'}}><CodeFillIcon className='iconos ' style={{color:'var(--color-secun)'}}></CodeFillIcon></SubTitle>
                    <div className='overlay'>
                    </div>   
                    <div className='overlayDos'>
                    </div> 
                    <div className='particles'>
                    </div>
                    <div className='particles dos'>
                    </div> 
                    <div className='particles tres'>
                    </div>

                    <div className='imgIs'>
                        <img src={Ilustracion}/>
                        <div className='bg-undraw'>
                        </div>
                    </div>
                    <SubTitle >Desarrollo Web</SubTitle>
                    <div className='box-services'>
                        <p className='description service'> - Desarrollo de interaccion con el usuario</p>
                        <p className='description service'> - Web Dinamica</p>
                        <p className='description service'> - Web Adaptable a cualquier dispositivo</p>
                     </div>                                                   
                </div>
                <div className='content_servicio segundo'>
                <SubTitle style={{margin:'20px 0', position:'relative', zIndex:10, top:'20px'}}><BookDesign className='iconos' style={{color:'var(--color-secun)'}}></BookDesign></SubTitle>
                    <div className='overlay'>
                    </div>   
                    <div className='overlayDos'>
                    </div> 
                    <div className='particles'>
                    </div>
                    <div className='particles dos'>
                    </div> 
                    <div className='particles tres'>
                    </div>

                    <div className='imgIs'>
                        <img src={Disign}/>
                        <div className='bg-undraw'>
                        </div>
                    </div>
                    <SubTitle >Diseño Web</SubTitle>
                    <div className='box-services'>
                        <p className='description service'> - Personalizar y diseñar su web</p>
                        <p className='description service'> - Diseños Modernos</p>
                     </div> 
                </div>                     
            </div>
        </Container>
    )
}

export default Servicios;