import Subtitles from '../Elementos/SubTitles';
import Face from 'remixicon-react/FacebookBoxFillIcon'
import Whats from 'remixicon-react/WhatsappFillIcon'
import Instagram from 'remixicon-react/InstagramFillIcon'
import Titulo from '../Elementos/Titulo';
import React,{useEffect} from 'react';
import ScrollReveal from 'scrollreveal';
const Footer = () =>{
   
    useEffect(
        ()=>{
         const sr = ScrollReveal({
             opacity:0,
             distance:'70px',
             origin:'top',
             reset:true
         })
         sr.reveal(
             '.subtitle-footer , .parrafo_footer, .about-me-footer, .span-footer, .content-socials'
         )
        }
    ,[])
    
    return(
        <footer className='footer'>
           <div className='contenedor-data'>
               <Subtitles className='subtitle-footer'>Desarrollador Web</Subtitles>
               <p className='description parrafo_footer'>Si tienes algo en mente contactame<br/>
                y trabajemos en esa idea</p>  
           </div>
           <div className='content-about-me-footer'>
                <Titulo className='about-me-footer' footer>Más de mi</Titulo>
                <span className='span-footer'>Acerca de mi</span>
           </div>
           <div className='content-socials'>
                    <Face className='social-footer'/>
                    <Whats className='social-footer'/>
                    <Instagram className='social-footer'/>
                </div>  
        </footer>    
    )

}


export default Footer;