import Container from '../Elementos/Container'
import Titulo from '../Elementos/Titulo';
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import Arrow from 'remixicon-react/ArrowRightLineIcon'
import React,{useEffect} from 'react'
import SwipeCore from 'swiper'
import 'swiper/swiper-bundle.min.css'
import 'swiper/swiper.min.css'
import PageTravel from '../img/Demon-PeruTravel.png'
import PageAkat from '../img/akat.png'
import Constructora from '../img/Constructora.png'
import SubTitle from '../Elementos/SubTitles'
import Btn from '../Elementos/Btn';
import ScrollReveal from 'scrollreveal';



SwipeCore.use([Navigation,Pagination])

const Works = () =>{

      useEffect(() => {
          
        const sr = ScrollReveal({
            opacity:0,
            origin:'top',
            distance:'80px',
            reset:true
        })

        sr.reveal(
            '.title-works, .slide-content-works',{
                opacity:0,
                interval:300
            }
        )
        



      }, [])
  

      const openPage = (page) =>{

         window.open(page)

      }

    return(

     <Container id='Works'>
         <Titulo about className='title-works'>Proyectos 
           <span className='about_span'>proyectos hechos</span>
         </Titulo>
         <Swiper
     modules={[Navigation, Pagination, Scrollbar, A11y]}
     spaceBetween={50}
     slidesPerView={1}
     navigation
     pagination={{ clickable: true }}
     onSwiper={(swiper) => console.log(swiper)}
     className='slide-content-works'
        >
      <SwiperSlide>
          <div className='view_work'>
             <div className='imgWork'>
               <img src={PageTravel}/>
              </div>
              <div className='content_description_view'>
                  <SubTitle left>Sitio Web Moderno</SubTitle>
                  <p className='description html_info' style={{padding:'15px 0' }}>
                      Sitio Web adaptable a todos los dispositivos
                      con animaciones interactivas
                  </p>
                  <Btn principal className='btn-demo' onClick={()=> openPage('https://abelsirius.github.io/PeruTravel/')}><span className='description text-btn-demo'>Demo</span> <Arrow className='arrow-demo'/></Btn>
              </div>  
          </div>

      </SwiperSlide>
      <SwiperSlide>
      <div className='view_work'>
             <div className='imgWork'>
               <img src={PageAkat}/>
              </div>
              <div className='content_description_view'>
                  <SubTitle left>Sitio Web Moderno</SubTitle>
                  <p className='description html_info' style={{padding:'15px 0' }}>
                      Sitio Web adaptable a todos los dispositivos
                      con animaciones interactivas
                  </p>
                  <Btn principal className='btn-demo'><span className='description text-btn-demo'>Demo</span> <Arrow className='arrow-demo'/></Btn>
              </div>                
          </div>
      </SwiperSlide>
      <SwiperSlide>
      <div className='view_work'>
             <div className='imgWork'>
               <img src={Constructora}/>
              </div>
              <div className='content_description_view'>
                  <SubTitle left>Sitio Web Moderno</SubTitle>
                  <p className='description html_info' style={{padding:'15px 0' }}>
                      Sitio Web adaptable a todos los dispositivos
                      con animaciones interactivas
                  </p>
                  <Btn principal className='btn-demo' onClick={()=> openPage('https://abelsirius.github.io/Constructora/')}><span className='description text-btn-demo'>Demo</span> <Arrow className='arrow-demo'/></Btn>
              </div>                
          </div>
      </SwiperSlide>
       </Swiper>
     </Container>
    )
}

export default Works;