import Titulo from "../Elementos/Titulo";
import Head from "../Elementos/Head";
import Nav from "../Elementos/Nav"
import Menu from "../Elementos/Menu";
import React,{useState} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars , faTimes, faMoon , faSun, fas} from '@fortawesome/free-solid-svg-icons'


const Header = () =>{
      
     const [iconMoon , cambiarIconMoon] = useState(false)

    const headerActive = () =>{
    

      if(window.scrollY > 0){
        document.querySelector('.header').classList.add('active')
      }
      else{
        document.querySelector('.header').classList.remove('active')

      }
    }

    window.addEventListener('scroll', headerActive)

   const [menuActive, cambiarMenuActive] = useState(false);


  const addClassActive = (e) =>{
    let navContent = document.querySelector('.content_nav');


    let items = document.querySelectorAll('.item_nav');
 
    items.forEach(
       item => item.classList.remove('active')
    )
    
     e.currentTarget.classList.add('active');
    
     if(navContent.classList.contains('active')){
      navContent.classList.remove('active')
      cambiarMenuActive(false)
    }

   }

       
     const onShow = (e,valor) =>{
      cambiarMenuActive(valor)

      document.querySelector('.content_nav').classList.toggle('active');


     }
     const cambiarIcon = (valor) =>{
          
         cambiarIconMoon(valor)

           
        if(valor){
          document.querySelector('body').classList.add('dark')
        }else{
          document.querySelector('body').classList.remove('dark')
        }

        
      }

    return(
      <Head className="header">
            <Titulo className="title-name">Abel</Titulo>
            <Menu className='menu-content'>
                 {
                   menuActive ? 
                   <FontAwesomeIcon icon={faTimes} className='menu_icon' 
                   onClick={(e)=> onShow(e,false)} />
                   :
                   <FontAwesomeIcon icon={faBars} className='menu_icon' onClick={(e)=>{
                    onShow(e,true)
                  }}/>
                 }
            </Menu>
            <Nav className='content_nav'>
              <ul className='content_items'>
                <li className='item_nav active' onClick={(e)=> addClassActive(e)}><a href='#' className='item_nav_text'>Inicio</a></li>
                <li className='item_nav' onClick={(e)=> addClassActive(e)}><a href='#About' className='item_nav_text'>Sobre mí</a></li>
                <li className='item_nav' onClick={(e)=> addClassActive(e)}><a href='#Skills' className='item_nav_text'>Habilidades</a></li> 
                <li className='item_nav' onClick={(e)=> addClassActive(e)}><a href='#Services' className='item_nav_text'>Servicios</a></li>                
                <li className='item_nav' onClick={(e)=> addClassActive(e)}><a href='#Works' className='item_nav_text'>Proyectos</a></li>
                <li className='item_nav' onClick={(e)=> addClassActive(e)}><a href='#Contact' className='item_nav_text'>Contactar</a></li>
                  {iconMoon ? 
                   <FontAwesomeIcon icon={faSun} onClick={()=> cambiarIcon(false)} className="icon-darkTheme"/>  
                   : <FontAwesomeIcon icon={faMoon} onClick={()=> cambiarIcon(true)} className="icon-darkTheme"/>
                   }            
              </ul>
            </Nav>
      </Head>
    )
}


export default Header;