import styled,{css} from 'styled-components';

const Btn = styled.button`
    
border:none;
outline:none;
background:var(--bg-btn);
font-size:13px;
font-weight:600;
color:var(--color-principal);
padding:10px;
border-radius:5px;
overflow:hidden;
position:relative;
z-index:10;
margin:10px 0;
cursor:pointer;
transition:.3s ease all;


&::before{
    content:'';
    position:absolute;
    z-index:-1;
    top:-10px;
    left:-200px;
    width:200%;
    height:200%;
    background:var(--color-subtitle);
    border-radius:30px;
    transition:.3s ease all;
    
}

&:hover::before{
    left:-20px;
}

&:hover{
    color:var(--color-hover);
}
${props => props.principal && css`
     
display:flex;
align-items:center;
color: #fff;
background: var(--color-parrafo)

&:hover{
    background:var(color-subtitle);
}
`}

`

export default Btn;