import Container from '../Elementos/Container'
import SubTitle from '../Elementos/SubTitles'
import Gps from 'remixicon-react/MapPin2FillIcon'
import Phone from 'remixicon-react/PhoneFillIcon'
import Mail from 'remixicon-react/MailLineIcon'
import Facebook from 'remixicon-react/FacebookFillIcon'
import WhatsApp from 'remixicon-react/WhatsappFillIcon'
import Btn from '../Elementos/Btn'
import Titulo from '../Elementos/Titulo'
import React,{useEffect} from 'react';
import ScrollReveal from 'scrollreveal'

const Contact = () =>{

      useEffect(
          ()=>{
           const sr = ScrollReveal(
               {
                origin:'top',
                distance:'90px',
                duration:2000,
                reset:true
               }
           )
           sr.reveal(
               '.title-contact, .content_sides_contact',
               {
                   opacity:0,
                   interval:300
               }
           )        
          }
      ,[])


    const onClick = (e) =>{
      e.currentTarget.classList.add('active');
    }

    return(
        <Container column style={{padding:'0px'}} id='Contact'>
            <Titulo about className='title-contact' >Contactar</Titulo>
           <div className='content_sides_contact'>
                <div className='info_contact'>
                     <SubTitle grande>Mantengámonos en contacto</SubTitle>
                     <p className='description html_info ' style={{padding:'15px 0' }}>Si tienes algo en mente contactame<br/>  y trabajemos en esa idea </p>
                     <div className='formas_contactar'>
                         <div className='forma_contactar'>
                             <Gps className='iconos' style={{marginRight:'15px'}}/>
                             <p className='description html_info ' style={{padding:'15px 0' }}>Libertad-Trujillo-Rinconada
</p>
                          </div> 
                          <div className='forma_contactar'>
                             <Phone className='iconos' style={{marginRight:'15px'}}/>
                             <p className='description html_info ' style={{padding:'15px 0' }}>922 260 216</p>

                          </div>
                          <div className='forma_contactar'>
                             <Mail className='iconos' style={{marginRight:'15px'}}/>
                             <p className='description html_info ' style={{padding:'15px 0' }}>abelgm.crack@gmail.com</p>

                          </div>  
                          <div className='overlay_cicle_two'>
                         </div>                                                           
                     </div>
                     <div className='social_contact'>
                         <SubTitle>Contactame :</SubTitle>
                         <div className='socials_contacts'
                            >
                           <Facebook className='iconos-socials'/>
                           <WhatsApp className='iconos-socials'/>
                         </div>    
                     </div>        
                </div>
                <form className='contact_form'>
                    <SubTitle textForm>Contactarme</SubTitle>
                    <div className='content_input ' onClick={(e)=> onClick(e)}>
                         <input type='text' />
                         <span className='text_form'>Nombre</span>
                    </div>
                    <div className='content_input ' onClick={(e)=> onClick(e)}>
                         <input type='email' />
                         <span className='text_form '>Gmail</span>
                    </div>
                    <div className='content_input ' onClick={(e)=> onClick(e)}>
                         <input type='email' />
                         <span className='text_form '>Numero</span>
                    </div>
                    <div className='content_input ' onClick={(e)=> onClick(e)}>
                         <textarea ></textarea>
                         <span className='text_form mensaje'>Mensaje</span>
                    </div>
                    <Btn principal className='btn-demo'>Enviar</Btn>                                       
                </form>     
           </div>
           <div className='overlay_circle'>
                    </div> 
        </Container>
    )
}

export default Contact;