import styled from "styled-components";


const Nav = styled.nav`
   position:absolute;
   top:0;
   right:0;
   z-index:100;
   transform: translateX(100%);
   width:40vw;
   height:100vh;
   transition: .7s ease all;
   box-shadow : 2px 0 0px 0px #1115;
   background:var(--bg-nav);

   ${props=> console.log(props)}
`

export default Nav;
